.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1;
  position: relative;
}

.i-left-wrapper {
  padding: 3.125rem;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 1.875rem;
  font-weight: 300;
}

.i-name {
  font-size: 3.75rem;
}

.i-title {
  height: 3.125rem;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-3.125rem);
  }
  50% {
    transform: translateY(-6.25rem);
  }
  75% {
    transform: translateY(-9.375rem);
  }
  100% {
    transform: translateY(12.5rem);
  }
}

.i-title-item {
  height: 3.125rem;
  font-size: 1.875rem;
  font-weight: bold;
  color: #59b256;
  display: flex;
  align-items: center;
}

.i-scroll {
  width: 1.875rem;
  height: 1.875rem;
  position: absolute;
  bottom: 1.25rem;
}

.i-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.i-bg {
  clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #59b256;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 30rem) {
  .i {
    flex-direction: column;
  }

  .i-left-wrapper {
  
    padding: 0.6rem;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  .i-desc{
    display: none;
  }
  
}
